import { useState }  from 'react'
import { useEffect } from 'react';
import Tasks from './Tasks';
import Button from './Button';
import AddTasks from './AddTasks';
const Header = ({title,color}) => {
const [listv,setListV] = useState([''])

    useEffect(()=>{
        const getTasks = async()=>{
            const taskfromserver = await fetchtask();
            setListV(taskfromserver);
        }

        getTasks();
    },[])

    const fetchtask = async()=>{
        const items =  await fetch("http://localhost:5000/tasks");
        const data = await items.json();
        return data;
    }


    async function AddTasks_List(tasks_list){

        const res = await fetch('http://localhost:5000/tasks',{
            method:'POST',
            headers:{
                'Content-type':'application/json'
            },
            body:JSON.stringify(tasks_list),
        })

        const data = await res.json();
        setListV([...listv,data]);
        console.log(tasks_list);
    }

    const [showAddTask,setshoWAddtask] = useState(true);

    function showTaskss(){
        console.log('h6ellow');
        setshoWAddtask(!showAddTask);
    }

    async function onClick(id){

        await fetch(`http://localhost:5000/tasks/${id}`,
        {
            method:'DELETE',
        })

        setListV(listv.filter(listid=> listid.id !==id))

    }

    return (
        <>
        <div className="container">
            <div className="row">
                <div className="col-md-7 mx-auto mt-4">
                    <header>
                        <div className="task-text" style={{color:color}}>
                                {title}
                        </div>
                        <div className="button">
                        {showAddTask ? (
                            <Button text="Add Task" style={{backgroundColor:'steelblue'}} myS={showTaskss}/>
                            ) : (<Button text="Show Task"  style={{backgroundColor:'red',color:'red'}} myS={showTaskss}/>)
                        }
                        </div>
                    </header>
                    {showAddTask ? (
                    <Tasks  onDel={onClick} myTasks={listv}/>) : ''}

                    {!showAddTask ? (<AddTasks selectAdd={showTaskss} onAdd={AddTasks_List}/>) : (<button type="button" className="btn btn-outline-info rounded-0 mx-auto mt-4 " onClick={showTaskss}> Show Add Task  </button>) }
                   
                    

                </div>
            </div>
        </div>
        </>
    )
}

Header.defaultProps ={
    title:'Task Tracker',
    color:'blue'
}

export default Header
