import {useState} from 'react';
import uuid from 'react-uuid';

const AddTasks = ({onAdd,selectAdd}) => {
    
const [task, setName] = useState('');
const [DayCompletion, setLastName] = useState('');

const [errorMessage,setErrorMessage] = useState('');

function handleSubmit(e){
    e.preventDefault();
    if(task==''){
        setErrorMessage('Please Enter Your Name');
        return false;
    }else if(DayCompletion==''){
        setErrorMessage('Please Enter Your day');
        return false;
    }else{
        onAdd({id:uuid(),task,DayCompletion});
        selectAdd();

    }
    


   
    
}
    return (
        <div>
            <form onSubmit={handleSubmit}>
                <div className="jumbotron mt-3 p-3" style={{backgroundColor:'steelblue'}}>
                    <div className="form-group">
                        <p>
                            {errorMessage}
                        </p>
                        <label htmlFor="exampleInputEmail1" className="text-white"> Task  </label>
                        <input type="text"  className="form-control" name="firstName" value={task} onChange={(e)=>setName(e.target.value)} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1" className="text-white"> DayToCompletion  </label>
                        <input type="text" className="form-control" name="lastName" value={DayCompletion} onChange={(e)=>setLastName(e.target.value)}/>
                    </div>
                    <button type="submit" className="text-white btn btn-outline-info"> Add Tasks </button>
                </div>
            </form>

        </div>
    )
}

AddTasks.propTypes = {

}

export default AddTasks
